.header-catalog{
  margin-left: auto;
  &__link,
  &__top{
    display: none;
  }
  &__inner{
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    margin-top: 1em;
    padding: 2.5em 1em;
    box-shadow: $shadow;
    z-index: 3;
    background-color: $white;
    border-radius: 1.25em;
    color: $black;
    @media screen and (min-width: 993px){
      opacity: 0;
      pointer-events: none;
    }
  }
  &__bg{
    display: none;
  }
  &__items{
    position: relative;
    min-height: 31.25em;
    @include grid100gap(1em);
  }
}

.menu-item{
  width: 22.25em;
  &__show{
    border-radius: 1.25em;
    background: $white;
    box-shadow: $shadow;
    padding: 1.75em;
    color: #808080;
    min-height: 9.6875em;
    display: flex;
    align-items: center;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    .text20{
      max-width: 8.5em;
    }
  }
  &__img{
    position: absolute;
    max-width: 13.25em;
    max-height: 100%;
    right: 0;
    bottom: 0;
  }
  &__hidden{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: calc(100% - 24em);
    opacity: 0;
    pointer-events: none;
  }
  &__groups{
    column-count: 3;
    column-gap: 1.75em;
  }
  &__close{
    display: none;
  }
  &._open{
    .menu-item__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.menu-group{
  display: inline-block;
  width: 100%;
  margin-bottom: .75em;
  font-family: $font2;
  &__links{
    margin-top: .625em;
    a{
      font-size: 1em;
      line-height: 120%;
    }
  }
}

.catalog-btn{
  padding: 0 1.75em;
  height: 2.875em;
  margin-right: 1em;
  img{
    width: 1.5em;
    margin-right: 1em;
  }
}

.header{
  &._open-catalog{
    .header-catalog__inner{
      opacity: 1;
      pointer-events: unset;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &._open-search{
      .header-catalog__inner{
        pointer-events: none;
        opacity: 0;
      }
    }
    &._open-catalog{
      .header-catalog{
        opacity: 1;
        pointer-events: unset;
      }
      .header-contacts{
        opacity: 0;
        pointer-events: none;
      }
      &~.content{
        pointer-events: none;
      }
    }
  }

  .header-catalog{
    position: fixed;
    top: 0;
    left: 0;
    padding: .625em 1em;
    width: 100%;
    opacity: 0;
    pointer-events: none;
    &__bg{
      display: block;
    }
    &__inner{
      background-color: transparent;
      padding: 0;
      position: relative;
      z-index: 5;
    }
    &__items{
      gap: .75em;
    }
    &__top{
      display: flex;
      align-items: center;
      justify-content: flex-end;
      position: relative;
      z-index: 4;
      margin-bottom: .625em;
    }
    &__link,
    &__close{
      display: flex;
    }
  }

  .menu-item{
    &._open{
      z-index: 4;
    }
    &__close{
      position: fixed;
      top: .625em;
      left: 1em;
      display: flex;
      z-index: 10;
    }
    &__hidden{
      position: fixed;
      top: 7.375em;
      left: 1em;
      width: calc(100% - 2em);
      height: calc(100 * var(--vh) - 8em);
      border-radius: 1.25em;
      background: $white;
      box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, 0.05);
      padding: .875em;
    }
    &__groups{
      height: 100%;
      overflow-y: auto;
      padding-right: .75em;
      @include scroll;
      column-count: 2;
    }
  }

  .catalog-btn{
    height: 1.875em;
    padding: 0 1em;
    img{
      width: 1.25em;
      margin-right: .625em;
    }
    span{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .menu-item{
    &__groups{
      column-count: 1;
      display: flex;
      flex-direction: column;
    }
  }
}
