.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .6;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        width: 46.375em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 1.875em;
        display: inline-block;
    }
    .close-btn{
        position: absolute;
        bottom: 100%;
        right: 0;
        margin-right: -1.5em;
    }
    .cart-form{
        padding: 0;
    }
}

.close-btn{
    background-color: transparent;
    width: 1.5em;
    @include center;
    flex-shrink: 0;
    img{
        width: 100%;
    }
}

.cart-form--type2{
    .cart-form__bottom{
        width: 100%;
    }
    @media screen and (min-width: 577px){
        .cart-form__checks{
            grid-template-columns: repeat(2,calc(50% - .5em));
            gap: .625em 1em;
        }
        .cart-form__btn{
            width: 21em;
            margin-left: auto;
            margin-right: auto;
        }
    }
}

.cart-form--type3{
    .cart-form__bottom{
        width: 100%;
    }
    @media screen and (min-width: 577px){
        .cart-form__btn{
            width: 100%;
            margin: 0;
            grid-row: 3/4;
            grid-column: 1/2;
            align-self: flex-start;
        }
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            padding: 1.5em 1em;
        }
        .close-btn{
            margin: 0 0 .31em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
    }

    .cart-form--type3{
        .filter-group__check{
            margin-top: .5em;
        }
        .cart-form__btn{
            margin-top: .5em;
        }
    }
}
