@import "../vars.scss";
.breadcrumbs {
    padding: 2.5em 0 4.375em;
    color: #808080;
    li{
        display: inline;
        position: relative;
        &:not(:first-child) {
            margin-left: 3em;
            &:before {
                content: '';
                position: absolute;
                top: .3125em;
                right: 100%;
                margin-right: 1.4375em;
                width: 0.375em;
                height: 0.375em;
                background-color: #808080;
                border-radius: 50%;
            }
        }
        &:last-child{
            pointer-events: none;
            color: $primary;
            &:before{
                background-color: $primary;
            }
        }
    }
    a,span{
        font-size: 1.125em;
        line-height: 120%;
    }
}

.breadcrumbs-slider{
    .swiper-slide{
        width: auto;
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: 1em 0 2.5em;
        ul{
            font-size: .75em;
        }
        a,span{
            line-height: 171%;
        }
    }

    .breadcrumbs-slider{
        width: calc(100% + 1.875em);
        padding: 0 .9375em;
        margin-left: -.9375em;
        overflow: hidden;
    }
}


