.header-search{
  margin-left: auto;
  width: 22.75em;
  position: relative;
  &__show{
    position: relative;
    z-index: 3;
  }
  .fg{
    input{
      border-radius: 6.25em;
      background: $dark;
      border-color: $dark;
      padding: 0 3em 0 1.19em;
      height: 2.875em;
      color: $white;
      &::placeholder{
        color: rgba($white,.4);
      }
    }
  }
  &__btn{
    position: absolute;
    right: .81em;
    top: 50%;
    transform: translateY(-50%);
    width: 1.25em;
    background-color: transparent;
    @include center;
    img{
      width: 100%;
    }
  }
  &__hidden{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    border-radius: 1.25em;
    background: #2A2A2A;
    padding-top: 2.875em;
    z-index: 2;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }
  &__inner{
    border-top: 1px solid #000;
    padding: .5em 1em 1em;
  }
  &__items{
    @include grid100gap(.81em);
    max-height: 45em;
    overflow-y: auto;
    @include scroll;
  }
  &__link{
    width: 100%;
    margin-top: 1.25em;
  }
}

.search-card{
  border-radius: 1.25em;
  border: 1px solid #D8D8D8;
  background: $white;
  box-shadow: $shadow;
  padding: .625em .625em .625em 0;
  display: grid;
  grid-template-columns: 8em calc(100% - 8.875em);
  gap: .875em;
  color: $black;
  &__img{
    height: 5.5em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  .product-card__available{
    position: static;
    margin-bottom: .44em;
  }
  &__desc{
    margin-top: .31em;
    margin-bottom: .625em;
  }
}

.header{
  &._open-search{
    .header-search__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
}


@media screen and (max-width: 992px){
  .header-search{
    width: 100%;
  }
}


