.why-card{
  padding: 1.75em;
  border-radius: 1.25em;
  background-image: url(../img/consult-icon.svg);
  background-repeat: no-repeat;
  background-position: right top;
  background-size: 0 auto;
  background-color: #F4F6F7;
  border: 1px solid #F4F6F7;
  box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, 0.05);
  transition: background-color .3s, background-image .1s;
  &__icon{
    height: 2.75em;
    margin-bottom: 2.75em;
  }
  &__title{
    max-width: 17em;
  }
  &:hover{
    background-size: 7.79em auto;
    background-color: transparent;
  }
}

.slider{
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper-container{
    width: calc(100% + 1em);
  }
  .swiper-wrapper{
    align-items: stretch;
  }
  .swiper-slide{
    height: auto;
    padding-right: 1em;
  }
}

.product-slider{
  margin-top: 1.875em;
  .slider__wrap{
    width: calc(100% + .75em);
    margin-left: -.375em;
    padding: .375em;
  }
  .swiper-slide{
    width: 25%;
  }
  .swiper-nav{
    position: absolute;
    bottom: 100%;
    right: 0;
    margin-bottom: 1.875em;
  }
}

.product-card{
  padding: 1.4375em;
  border-radius: 1.25em;
  border: 1px solid #D8D8D8;
  background: $white;
  box-shadow: $shadow;
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  &__img{
    @include center;
    padding: 0 .44em;
    height: 13em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  .favourite-btn{
    margin-bottom: .625em;
  }
  &__action{
    position: absolute;
    top: 1.375em;
    right: 1.5em;
  }
  &__available{
    position: absolute;
    top: 1.375em;
    left: 1.5em;
    pointer-events: none;
    i{
      width: .25em;
      height: .25em;
      margin-right: .375em;
      flex-shrink: 0;
    }
  }
  &__info{
    margin-top: .75em;
    @include grid100gap(.625em);
  }
  &__props{
    margin-top: .625em;
  }
  &__prop{
    display: flex;
    justify-content: space-between;
    gap: 1em;
    &:not(:last-child){
      padding-bottom: .44em;
      border-bottom: 1px solid #E2E2E2;
      margin-bottom: .31em;
    }
  }
  &__bottom{
    margin-top: auto;
    display: flex;
    align-items: center;
    padding-top: 1.5em;
    gap: 1.25em;
  }
  &__cart{
    img{
      width: 1.25em;
    }
  }
  &__btn{
    flex-grow: 1;
  }
  .cartcalc{
    display: none;
  }
  &._added-to-cart{
    .product-card__btn{
      display: none;
    }
    .cartcalc{
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .why{
    &__top{
      text-align: center;
    }
  }

  .why-card{
    padding: 1.1875em;
    min-height: 9.625em;
    background-image: unset;
    &__icon{
      margin-bottom: .625em;
    }
  }

  .slider{
    .swiper-container{
      width: calc(100% + .5em);
    }
    .swiper-slide{
      padding-right: .5em;
    }
  }

  .product-collection{
    overflow: hidden;
    .h2{
      text-align: center;
    }
  }

  .product-slider{
    margin-top: 1.5em;
    .slider__wrap{
      overflow: unset;
    }
    .swiper-slide{
      width: 23em;
      max-width: 100%;
    }
    .swiper-nav{
      position: static;
      justify-content: center;
      margin-top: 1.25em;
    }
  }

  .product-card{
    &__info{
      gap: .31em;
    }
  }
}


