.catalog-item{
  &__title{
    max-width: 50em;
  }
  &__top{
    &-col{
      grid-column: span 2;
    }
  }
  &__bottom-col{
    grid-column: span 3;
  }
}

.m-card,
.m-card2{
  border-radius: 1.25em;
  background: $white;
  box-shadow: $shadow;
}

.m-card{
  border: 1px solid #D8D8D8;
}

.gallery{
  position: relative;
  height: 30em;
  padding: 1.25em 2em 1.25em 1em;
  display: grid;
  grid-template-columns: 11.375em calc(100% - 11.375em - 1.875em);
  gap: 1.875em;
  align-items: center;
  &__action{
    position: absolute;
    top: 1.875em;
    right: 1.875em;
    z-index: 3;
  }
  .favourite-btn{
    margin-bottom: .625em;
  }
  &__small,
  &__big{
    height: 27.5em;
  }
  .swiper{
    height: calc(100% + 1em);
  }
  .swiper-slide{
    padding-bottom: 1em;
  }
  &__img{
    width: 100%;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__big{
    .swiper-slide{
      @include center;
    }
  }
  &__small{
    .swiper-slide{
      height: calc(100% / 3);
      &-thumb-active .gallery__img{
        opacity: 1;
      }
    }
    .gallery__img{
      cursor: pointer;
      opacity: .4;
      padding: .875em;
      border-radius: 1.25em;
      border: 1px solid #D8D8D8;
      background: $white;
      box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, 0.05);
    }
  }
}

.props{
  padding: 1.125em;
  min-height: 100%;
  &__items{
    margin-top: 1em;
    @include grid100gap(1em);
    &:first-child{
      margin-top: 0;
    }
  }
}

.prop-item{
  display: flex;
  justify-content: space-between;
  gap: 1em;
}


@media screen and (max-width: 992px){
  .catalog-item{
    &__top{
      margin-top: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .5em));
    }
    &__bottom{
      grid-template-columns: 100%;
      &-col{
        grid-column: span 1;
      }
    }
  }

  .gallery{
    &__action{
      top: .9375em;
      right: .9375em;
    }
  }

  .props{
    &__items{
      gap: .5em;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    &__top{
      grid-template-columns: 100%;
      &-col{
        grid-column: span 1;
      }
      &-col2{
        order: 2;
      }
    }
  }

  .gallery{
    height: unset;
    padding: 1em 1em 1.44em;
    grid-template-columns: 100%;
    gap: .625em;
    overflow: hidden;
    &__small,
    &__big{
      height: unset;
    }
    .swiper{
      height: 100%;
      width: calc(100% + .625em);
      overflow: unset;
    }
    .swiper-slide{
      padding: 0 .625em 0 0;
    }
    &__big{
      overflow: hidden;
      order: -1;
      .gallery__img{
        height: 14em;
      }
    }
    &__small{
      .swiper-slide{
        width: 8.5em;
        height: unset;
      }
      .gallery__img{
        padding: .625em;
      }
    }
  }
}
