.main-reviews{
  &__img{
    position: absolute;
    left: 0;
    bottom: 0;
    height: 36.5em;
    width: auto;
  }
  &__inner{
    padding: 6.5em 0 0;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1.25em 1em;
  }
  &__col{
    max-width: 42em;
  }
  &__col2{
    padding: 1.875em .81em 1.875em 1.875em;
    border-radius: 1.25em 1.25em 0 0;
    background-color: $white;
    color: $dark;
  }
  &__items{
    @include grid100gap(1em);
    margin-top: 1.625em;
    max-height: 36em;
    overflow-y: auto;
    @include scroll;
    padding-right: 2em;
  }
}

.review-card{
  display: grid;
  grid-template-columns: calc(100% - 10.5em) 7.5em;
  gap: .625em 3em;
  padding-bottom: 1.25em;
  border-bottom: 1px solid #F6F6F6;
  &:last-child{
    border: none;
    padding-bottom: 0;
  }
  &__img{
    border-radius: .3125em;
    border: 1px solid #D6D7D7;
    @include imgCover(132%);
    &:hover{
      border-color: $primary;
      &:after{
        opacity: 1;
      }
    }
    span{
      display: none;
    }
    &:after{
      content: '';
      @include centerAbsolute;
      width: 2.75em;
      height: 2.75em;
      background-image: url(../img/search.svg);
      @include bgImgSize(100%);
      transition: all .3s;
      opacity: 0;
    }
  }
}

.main-projects{
  &__top{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__info{
    max-width: 22.75em;
  }
  &__items{
    margin-top: 2.19em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }
}

.norm-btn{
  min-width: 22.75em;
}

.project-card{
  position: relative;
  color: $white;
  &__content{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding: 1.25em;
  }
  &__img{
    border-radius: 1.25em;
    @include imgCover(51%);
    box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, 0.05);
    &:after{
      transition: all .3s;
    }
  }
  &__top{
    display: flex;
    flex-wrap: wrap;
    gap: 1em;
    margin-bottom: auto;
  }
  &__bottom{
    &:hover{
      color: $white;
    }
  }
  &__tag{
    border-radius: 12.5em;
    background: rgba(219, 221, 223, 0.30);
    backdrop-filter: blur(20.75px);
    padding: 0 1em;
    height: 2.4375em;
    display: flex;
    align-items: center;
    img{
      width: 1.25em;
      margin-right: .625em;
    }
  }
  &__title{
    padding: .625em 1em;
    border-radius: 12.5em;
    transition: all .3s;
    font-size: 1em;
  }
  &__btn{
    width: 2.75em;
    height: 2.75em;
    @include center;
    border-radius: 50%;
    transition: all .3s;
    margin-left: 1.25em;
    flex-shrink: 0;
    img{
      width: 1.625em;
    }
  }
  &__text{
    padding: .625em 1em;
    margin-bottom: -.625em;
    display: none;
  }
  &:hover{
    .project-card__img:after{
      opacity: 0;
    }
    .project-card__btn,
    .project-card__title{
      background-color: $primary;
    }
    .project-card__text{
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .main-reviews{
    border-radius: 2.5em 2.5em 0 0;
    &__img{
      right: -17em;
      left: unset;
      height: 15em;
    }
    &__col2{
      padding: 1em .5em 1em 1em;
    }
    &__top{
      max-width: 16.75em;
    }
    &__items{
      margin-top: 1em;
      max-height: 35em;
      padding-right: 1em;
    }
  }

  .review-card{
    grid-template-columns: 100%;
    padding-bottom: 1em;
    &__col2{
      display: flex;
    }
    &__img{
      border-radius: 12.5em;
      background: rgba(219, 221, 223, 0.30);
      backdrop-filter: blur(20.75px);
      border: none;
      padding: 0 1em;
      height: 1.875em;
      color: #B7B9B9;
      @include center;
      span{
        display: block;
      }
      img,
      &:after{
        display: none;
      }
    }
  }

  .main-projects{
    &__items{
      margin-top: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
    }
  }

  .norm-btn{
    min-width: unset;
  }

  .project-card{
    &__img{
      padding-bottom: 104%;
    }
    &__top{
      gap: .5em;
    }
    &__tag{
      height: 2.125em;
    }
    &:hover{
      .project-card__text{
        display: none;
      }
    }
  }

}


@media screen and (max-width: 576px){
  .main-reviews{
    &__img{
      height: 7.875em;
    }
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .main-projects{
    &__inner{
      position: relative;
      padding-bottom: 3.75em;
    }
    &__top{
      display: block;
    }
    &__info{
      text-align: center;
      max-width: 100%;
    }
    &__link{
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
    }
    &__items{
      grid-template-columns: 100%;
    }
    &__link2{
      width: 100%;
      min-width: unset;
    }
  }
}
