.pagination{
  margin-top: 2.5em;
  display: flex;
  justify-content: center;
  color: $blueGray;
  ul{
    display: flex;
    justify-content: center;
  }
  li{
    margin: 0 .375em;
  }
  .page-numbers,
  a{
    font-size: 1em;
    height: 2.75em;
    min-width: 2.75em;
    padding: 0 .3125em;
    @include center;
    border: 1px solid #D8D8D8;
    border-radius: 1.5em;
    &:hover{
      color: $blueGray;
      background-color: rgba($primary,.2);
    }
  }
  .current a{
    background-color: $primary;
    border-color: $primary;
    color: $white;
    pointer-events: none;
  }
  .prev,
  .next{
    @include bgImgSize(1.625em);
    a{
      background-color: transparent;
    }
  }
  ._disabled{
    opacity: .7;
  }
  .prev{
    background-image: url(../img/prev.svg);
  }
  .next{
    background-image: url(../img/next.svg);
  }
}


@media screen and (max-width: 992px){
  .pagination{
    margin-top: 1.25em;
  }
}


