.compare{
  &__grid{
    margin-bottom: 3.125em;
  }
  &__col{
    align-self: flex-end;
    .compare-card__prop{
      background-color: $white;
    }
  }
  &__col2{
    grid-column: span 3;
  }
}

.compare-slider{
  .swiper-slide{
    width: calc(100% / 3);
  }
  .swiper-scrollbar{
    position: absolute;
    top: 100%;
    width: 100%;
    margin-top: 3em;
    height: .375em;
    background-color: #E0E0E0;
    &-drag{
      background-color: #86969A;
    }
  }
}

.compare-card{
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .product-card__desc,
  .product-card__title{
    @include truncateLine;
  }
  &__btn{
    margin-top: .875em;
    width: 100%;
  }
  &__bottom{
    margin-top: 1em;
  }
  &__props{
    @include grid100gap(1em);
  }
  &__prop{
    display: flex;
    align-items: center;
    height: 4.125em;
    padding: .25em 2.5em;
    border-radius: 1.25em;
    background: #F4F6F7;
    box-shadow: $shadow;
  }
}


@media screen and (max-width: 992px){
  .compare{
    overflow: hidden;
    &__grid{
      margin-bottom: 2.125em;
      display: block;
      position: relative;
    }
    &__col{
      width: 5.25em;
      position: absolute;
      left: 0;
      bottom: 0;
      pointer-events: none;
      z-index: 2;
      .compare-card__bottom{
        padding: 0;
      }
    }
    &__col2{
      grid-column: span 1;
    }
  }

  .compare-slider{
    .slider__wrap{
      overflow: unset;
    }
    .swiper-slide{
      width: 23em;
    }
    .swiper-scrollbar{
      margin-top: 2em;
    }
  }

  .compare-card{
    &__bottom{
      padding-left: 5.75em;
    }
    &__props{
      gap: .625em;
    }
    &__prop{
      height: 2.5em;
      padding: .125em .875em;
    }
  }
}


