.product-info{
  padding: 1em 1.5em;
  min-height: 100%;
  &__available{
    img{
      width: 1.5em;
      margin-right: .5em;
    }
  }
  .product-card__props{
    font-weight: 700;
    margin-top: 1.25em;
    font-family: $font2;
    color: $blueGray;
  }
  &__cart{
    display: flex;
    gap: 1em;
    margin-top: 1.875em;
    .m-btn{
      flex-grow: 1;
      padding: 0;
    }
  }
  &__btn{
    width: 100%;
    margin-top: 1.25em;
  }
  &__advantages{
    margin-top: 1.5em;
    @include grid100gap(.75em);
    &-item{
      display: flex;
      align-items: center;
      img{
        width: 1.75em;
        margin-right: .625em;
        flex-shrink: 0;
      }
    }
  }
}

.product-desc{
  margin-bottom: 1em;
  &:last-child{
    margin-bottom: 0;
  }
  &__top{
    font-size: 1em;
    border-radius: 6.25em;
    border: 1px solid #808080;
    background: #EF3B24;
    @include center;
    padding: .5em;
    min-height: 2.875em;
    color: $white;
    font-weight: 700;
    margin-bottom: 1em;
  }
  &__bottom{
    padding: 1.25em;
  }
  &__quote{
    padding: 1.25em;
    margin-top: 1.25em;
    border-radius: 1.25em;
    background: #F4F6F7;
    box-shadow: $shadow;
  }
  &__items{
    margin-top: 1.25em;
    padding: 0 1.25em;
    display: flex;
    flex-wrap: wrap;
    gap: 1em 2.5em;
  }
  &__item{
    display: flex;
    align-items: center;
    min-height: 1.375em;
    background-image: url(../img/logo2.svg);
    background-repeat: no-repeat;
    background-size: 1.0625em auto;
    background-position: left center;
    color: $gray3;
    padding-left: 1.81em;
  }
}

.product-info2{
  padding: 1.25em;
  &__btn{
    width: 100%;
    margin: 1.5em 0;
  }
  &__doc{
    border-radius: 1.25em;
    background: #F4F6F7;
    box-shadow: $shadow;
    padding: 1.25em;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-top: .625em;
    img{
      margin-left: 3.625em;
      width: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .product-info{
    padding: 1.25em;
    &__top{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__price{
      order: -1;
      margin: 0;
      .h2{
        font-size: 1.75em;
      }
    }
    .product-card__props{
      margin-top: 1em;
    }
    &__cart{
      gap: 1.625em;
      margin-top: 1.25em;
    }
    &__btn{
      margin-top: 1em;
    }
    &__advantages{
      margin-top: 1.25em;
    }
  }

  .product-desc{
    &__bottom{
      padding: .875em;
    }
    &__quote{
      padding: .875em 1em;
      margin-top: 1em;
    }
    &__items{
      gap: .5em 1em;
      margin-top: 1em;
      padding: 0;
    }
  }

  .product-info2{
    &__btn{
      margin: 1.25em 0;
    }
  }
}


@media screen and (max-width: 576px){
  .product-desc{
    &__items{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .5em));
    }
  }
}
