.we-done{
  padding: 3.125em 0;
  border-radius: 6.25em 6.25em 0 0;
  background: #080A0B;
  color: $white;
  &__desc{
    max-width: 43em;
  }
  &__img{
    width: 79em;
    max-width: 100%;
    margin: -4.5em 0 0 auto;
    display: block;
  }
}

.history{
  &__items{
    @include grid100gap(1em);
    margin-top: 3.125em;
  }
}

.history-item{
  padding: 2.5em 3.31em;
  display: grid;
  grid-template-columns: 8.5em calc(100% - 8.5em);
  align-items: center;
  transition: all .3s;
  &:nth-child(odd){
    background-color: #F4F6F7;
  }
  &__year{
    font-size: 1.875em;
    line-height: 110%;
    color: $primary;
    font-weight: 900;
  }
}


@media screen and (max-width: 992px){
  .we-done{
    padding: 2.125em 0 2em;
    border-radius: 2.5em 2.5em 0 0;
    &__img{
      margin-top: 1.25em;
    }
  }

  .history{
    &__items{
      gap: .5em;
      margin-top: 1.25em;
    }
  }

  .history-item{
    padding: .875em;
    grid-template-columns: 100%;
    gap: .75em;
  }
}


