@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
._flex,
._flex-between,
._flex-center{
  display: flex;
  align-items: center;
  &._start{
    align-items: unset;
  }
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

._mt48{
  margin-top: 3em;
}

._mt40{
  margin-top: 2.5em;
}

._mt32{
  margin-top: 2em;
}

._mt30{
  margin-top: 1.875em;
}

._mt24{
  margin-top: 1.5em;
}

._mt22{
  margin-top: 1.375em;
}

._mt20{
  margin-top: 1.25em;
}

._mt16{
  margin-top: 1em;
}

._mt12{
  margin-top: .75em;
}

._mt10{
  margin-top: .625em;
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.32);
    pointer-events: none;
    border-radius: inherit;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2.6875em;
    height: 2.6875em;
    @include lastM(0 .625em 0 0);
    @include center;
    svg{
      width: 100%;
    }
    &:hover{
      g,path{
        opacity: 1;
      }
      ._fill{
        fill: $black;
        stroke-opacity: 1;
      }
      ._fill2{
        fill: $white;
      }
    }
  }
}

.compare-btn,
.favourite-btn{
  @include center;
  border-radius: 50%;
  background: #F4F6F7;
  width: 2.75em;
  height: 2.75em;
  @include bgImgSize(1.25em);
  background-size: 1.25em auto, 0 auto;
  transition: all 0s;
  &._added{
    background-size: 0 auto, 1.25em auto;
  }
}

.favourite-btn{
  background-image: url(../img/favourite.svg),url(../img/favourite2.svg);

}

.compare-btn{
  background-image: url(../img/compare.svg),url(../img/compare2.svg);
}




@media screen and (max-width: 992px){
  ._mt30-tablet{
    margin-top: 1.875em;
  }

  ._mt20-tablet{
    margin-top: 1.25em;
  }

  ._mt16-tablet{
    margin-top: 1em;
  }

  ._mt10-tablet{
    margin-top: .625em;
  }

  ._mt8-tablet{
    margin-top: .5em;
  }
}


