@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.19em;
    height: 3.5625em;
    border-radius: 2.5em;
    font-family: $font2;
    &:focus {
        box-shadow: none;
    }
    &--small{
        height: 2.75em;
    }
}

.m-btn-red{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary,10%);
        color: $white;
    }
}

.m-btn-red-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}

.m-btn-dark{
    background-color: $dark;
    color: $white;
    &:hover{
        background-color: lighten($dark,10%);
        color: $white;
    }
}

.m-btn-dark-outline{
    border: 1px solid $dark;
    color: $dark;
    &:hover{
        background-color: $dark;
        color: $white;
    }
}

.m-btn-gray-outline{
    border: 1px solid #808080;
    color: #808080;
    &:hover{
        background-color: $dark;
        border-color: $dark;
        color: $white;
    }
}

.m-btn-gray{
    background-color: #F4F6F7;
    color: $black;
    &:hover{
        background-color: darken(#F4F6F7,10%);
        color: $black;
    }
}

.round-btn{
    width: 2.75em;
    height: 2.75em;
    @include center;
    border-radius: 50%;
    flex-shrink: 0;
    transition: all .3s;
    svg,
    img{
        width: 1.625em;
    }
}

.swiper-nav{
    display: flex;
    gap: 1em;
}

.swiper-button{
    margin: 0;
    position: static;
    background: #F4F6F7;
    &:hover{
        background-color: $primary;
        path{
            stroke: $white;
            stroke-opacity: 1;
        }
    }
    &.swiper-button-disabled{
        opacity: 1;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 2.5em;
    }
}
