.reviews{
  .review-card{
    border-radius: 1.25em;
    background: $white;
    box-shadow: $shadow;
    padding: 1em 1em 1em 1.875em;
    grid-template-columns: calc(100% - 14.875em) 11.85em;
  }
}

.article{
  &__top{
    max-width: 66em;
  }
  &__tags{
    margin-top: .3125em;
    display: flex;
    gap: 1em;
  }
  .project-card__tag{
    color: #909090;
  }
  &__col2{
    grid-column: span 3;
  }
  .consult-form{
    padding: 1.25em;
    border-radius: 1.25em;
    box-shadow: $shadow;
    margin-top: 1em;
  }
  .consult-info{
    margin-top: 1em;
  }
  &__img{
    margin-bottom: 1em;
    img{
      width: 100%;
      display: block;
      border-radius: 1.25em;
    }
    ._view2{
      display: none;
    }
  }
}

.article-content{
  padding: 1.25em;
  border: none;
  &__title{
    color: #808080;
  }
  &__links{
    margin-top: .75em;
    @include grid100gap(.75em);
    li{
    }
    a{
      display: flex;
      align-items: flex-start;
      gap: .31em;
      font-size: 1em;
      line-height: 120%;
    }
    span{
      &:first-child{
        @include sameNumWidth;
      }
    }
    ul{
      margin-top: .625em;
      @include grid100gap(.625em);
      color: $primary;
      a{
        font-size: .875em;
      }
    }
  }
}

.article-steps{
  @include grid100gap(.625em);
  position: relative;
  &:before{
    content: '';
    position: absolute;
    height: 100%;
    top: 0;
    left: 2.375em;
    transform: translateX(-50%);
    border-left: 2px dashed #939393;
    z-index: -1;
  }
  &__item{
    display: flex;
    align-items: center;
    &-num{
      height: 2.5em;
      width: 4.75em;
      flex-shrink: 0;
      @include center;
      border-radius: 12.5em;
      background: rgba(219, 221, 223, 0.30);
      backdrop-filter: blur(20.75px);
      color: #909090;
      margin-right: .75em;
    }
    &-text{
      font-size: 1em;
      line-height: 138%;
    }
  }
}



@media screen and (max-width: 992px){
  .reviews{
    .review-card{
      padding: 1em;
      grid-template-columns: 100%;
    }
  }

  .article{
    &__top{
      text-align: center;
    }
    &__tags{
      margin-top: 1em;
      justify-content: center;
    }
    &__grid{
      gap: 1.25em;
    }
    &__col2{
      grid-column: span 1;
    }
    .consult-info,
    .consult-form{
      display: none;
    }
  }

  .article-content{
    padding: 1em;
    &__links{
      margin-top: .625em;
      gap: .625em;
      a{
        font-size: .875em;
      }
      ul{
        gap: .3125em;
        a{
          font-size: .75em;
        }
      }
    }
  }

  .article-steps{
    gap: 1em;
    &:before{
      display: none;
    }
    &__item{
      display: block;
      &-num{
        margin: 0 0 .625em;
        height: 2.125em;
        width: 4.75em;
        .text16{
          font-size: .75em;
        }
      }
      &-text{
        font-size: .875em;
      }
    }
  }


}


@media screen and (max-width: 576px){
  .article{
    &__img{
      margin-bottom: .625em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}
