.clients,
.main-reviews{
  padding: 0;
  border-radius: 6.25em 6.25em 0 0;
  background: #080A0B;
  color: $white;
  position: relative;
  overflow: hidden;
  &__inner{
    padding: 5em 0 4.25em;
    position: relative;
    z-index: 2;
  }
}

.clients{
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    gap: 1.5em 1em;
    margin: 3.75em 0 0 -16.125em;
    max-width: 70.25em;
  }
}

.client-card{
  padding: 3.75em 1.25em 1em;
  border-radius: 1.25em;
  background: #2A2A2A;
  color: rgba(255, 255, 255, 0.30);
  &__img{
    height: 6em;
    width: 9.75em;
    @include center;
    margin: 0 auto 3em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.docs{
  &__top{
    max-width: 22.75em;
  }
  &__items{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 2.8125em / 4));
    gap: .9375em;
  }
}

.doc-card{
  border-radius: 1.25em;
  border: 1px solid #DED9D6;
  box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, 0.05);
  padding: 1.5em;
  min-height: 13em;
  display: flex;
  flex-direction: column;
  &__title{
    line-height: 140%;
  }
  &__nav{
    padding-top: 1.5em;
    margin-top: auto;
    display: flex;
    gap: 1em;
  }
  &__btn{
    min-width: 11.3125em;
    height: 2.75em;
    &--download{
      min-width: unset;
      padding: 0 1em;
      color: $blueGray;
    }
    img{
      width: 1em;
      margin-left: .5em;
    }
  }
}


@media screen and (max-width: 992px){
  .clients{
    border-radius: 2.5em 2.5em 0 0;
    &__inner{
      padding: 2.125em 0 1.875em;
    }
    &__items{
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
      gap: .5em;
      margin: 1.5em 0 0;
    }
    &__img{
      top: 6.625em;
      right: -2.5em;
      height: 20em;
    }
  }

  .client-card{
    padding: .3125em .825em .8125em;
    &__img{
      height: 4.375em;
      width: 7.25em;
      margin-bottom: 1em;
    }
  }

  .docs{
    &__items{
      margin-top: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
    }
  }

  .doc-card{
    padding: 1.25em;
    min-height: 10.5625em;
  }
}


@media screen and (max-width: 576px){
  .clients{
    &__top{
      max-width: 18.5em;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .25em));
    }
    &__img{
      height: 15em;
    }
  }

  .docs{
    &__top{
      max-width: 100%;
      text-align: center;
    }
    &__items{
      grid-template-columns: 100%;
    }
  }
}
