.condition-item{
  display: flex;
  align-items: center;
  min-height: 8em;
  padding: 1.5em 1.5em 1.5em 4.625em;
  background-image: url(../img/condition-icon.svg);
  background-size: 3.75em auto;
  background-position: left center;
  background-repeat: no-repeat;
}

.condition-info{
  padding: 1em;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    border-radius: 1.25em 0 0 1.25em;
    overflow: hidden;
    img{
      height: 100%;
      display: block;
    }
  }
  &__link{
    margin-top: .875em;
    width: 22.75em;
  }
}

.delivery-calc{
  position: relative;
  min-height: 36em;
  padding: 3.75em 3.125em;
  display: flex;
  align-items: flex-end;
  color: $white;
  &__content{
    width: 63.875em;
    max-width: 100%;
  }
  &__desc{
    max-width: 44.875em;
  }
}

.calc-form{
  padding: 2.125em;
  margin-top: 1.875em;
  border-radius: 2.5em;
  background: #2A2A2A;
  box-shadow: $shadow;
  backdrop-filter: blur(11.949999809265137px);
  &__bottom,
  &__fields{
    display: grid;
    grid-template-columns: 22.75em 17.4375em 17.4375em;
    justify-content: space-between;
  }
  .fg{
    input{
      border-color: rgba($white,.5);
      color: $white;
      &::placeholder{
        color: rgba($white,.5);
      }
      &:focus{
        background-color: transparent;
      }
    }
  }
  .fg-white{
    input{
      border-color: rgba(39, 39, 39, 0.30);
      background-color: $white;
      color: $black;
      &::placeholder{
        color: $black;
      }
      &:focus{
        background-color: $white;
      }
    }
  }
  &__bottom{
    margin-top: .625em;
    align-items: center;
  }
  &__info{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: .5em;
    grid-column: span 2;
    &:after{
      content: '';
      min-width: 1em;
      flex-grow: 1;
      height: 100%;
      border-bottom: 2px dashed rgba($white,.5);
      margin-bottom: .5em;
    }
  }
  &__price{
    order: 2;
  }
}


@media screen and (max-width: 992px){
  .condition-item{
    min-height: 5.25em;
    padding: .75em .75em .75em 4.875em;
  }

  .condition-info{
    grid-column: 1/-1;
    padding: 1em .75em 1em 8.25em;
    margin-bottom: 3.75em;
    align-items: flex-start;
    justify-content: center;
    min-height: 5.25em;
    &__link{
      position: absolute;
      top: 100%;
      left: 0;
      margin-top: 1.25em;
      width: 100%;
    }
    &__img{
      img{
        transform: translateX(-4em);
      }
    }
  }

  .delivery-calc{
    min-height: 32em;
    padding: 1.25em 1em;
    &__desc{
      margin-top: .5em;
    }
    .produce-card__img:after{
      background: linear-gradient(169deg, rgba(0, 0, 0, 0.00) 13.3%, #000 32.06%);
    }
  }

  .calc-form {
    padding: 0;
    margin-top: .8125em;
    border-radius: 0;
    background: transparent;
    box-shadow: unset;
    backdrop-filter: unset;
    &__bottom,
    &__fields {
      grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
      gap: .5em;
    }
    .fg{
      input{
        padding: 0 1.25em;
      }
    }
    &__info{
      &:after{
        margin-bottom: .25em;
      }
      .h2{
        font-size: 1.25em;
      }
    }
    &__btn{
      padding: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .calc-form {
    &__bottom,
    &__fields {
      grid-template-columns: 100%;
    }
    &__btn{
      order: 2;
    }
    &__info{
      grid-column: span 1;
    }
    &__bottom{
      gap: 1.25em;
      margin-top: .875em;
    }
  }
}
