.text-center{
  text-align: center;
}

.caps-text{
  text-transform: uppercase;
}

.underline-text{
  text-decoration: underline;
}

.h1{
  font-size: 2.75em;
  line-height: 120%;
  letter-spacing: -.01em;
  text-transform: uppercase;
  font-weight: 700;
}

.h1-small{
  font-size: 1.875em;
}

.h2{
  font-size: 2em;
  line-height: 110%;
}

.text24{
  font-size: 1.5em;
  line-height: 120%;
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 120%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text p,
.text li,
.footer-nav a,
.text16{
  font-size: 1em;
  line-height: 120%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 120%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.font2{
  font-family: $font2;
}

.font3{
  font-family: $font3;
}

.dark-text{
  color: $dark;
}

.red-text{
  color: $primary;
}

.gray-text{
  color: $gray;
}

.gray-text2{
  color: $gray2;
}

.gray-text3{
  color: $gray3;
}

.blue-gray-text{
  color: $blueGray;
}

.green-text{
  color: $green;
}

._no-wrap{
  white-space: nowrap;
}


@media screen and (max-width: 992px){
  .h1{
    font-size: 1.75em;
  }

  .h1-mob-small,
  .h2-tablet,
  .h2{
    font-size: 1.375em;
  }

  .h2-tablet{
    font-weight: 400;
    text-transform: unset;
    letter-spacing: unset;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text p,
  .text li,
  .footer-nav a,
  .text14-tablet{
    font-size: .875em;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

  .text-center-mob{
    text-align: center;
  }
}
