.main-categories{
  padding-top: 7.25em;
  border-radius: 6.25em 6.25em 0 0;
  background: #FDFDFD;
  position: relative;
  z-index: 2;
  @media screen and (min-width: 993px){
    .category-card{
      &:first-child{
        grid-column: span 2;
        .category-card__img{
          max-height: 16em;
          max-width: 26em;
        }
      }
    }
  }
  &__more{
    display: none;
  }
}

.category-card{
  border-radius: 1.25rem;
  background: $white;
  box-shadow: $shadow;
  padding: 1.25em;
  min-height: 20.625em;
  position: relative;
  overflow: hidden;
  display: block;
  &__img{
    position: absolute;
    right: 0;
    bottom: 0;
    max-height: 12em;
    max-width: 100%;
  }
  &__info{
    position: relative;
    z-index: 2;
    @include grid100gap(1.125em);
    max-width: 20em;
  }
  &__count{
    font-style: italic;
    color: rgba(128, 128, 128, 0.50);
  }
}

._toggle-more-parent{
  ._toggle-more{
    ._view2{
      display: none;
    }
  }
  &._show-all{
    ._toggle-more{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}

.main{
  background: linear-gradient(0deg, #000 0%, #000 100%), #D9D9D9;
  margin-bottom: -7.25em;
  padding: 12em 0 11.625em;
  color: $white;
}

.main-slider,
.product-slider{
  .swiper-pagination{
    bottom: 1.875em;
    z-index: 2;
  }
}

.main-slider{
  border-radius: 3.75em;
  border: 2px solid $primary;
  background: #06080B;
  box-shadow: 19px 14px 20px 0 rgba(239, 59, 36, 0.15);
}

.main-card{
  min-height: 35em;
  display: flex;
  align-items: center;
  padding: 5.81em;
  overflow: hidden;
  border-radius: 3.75em;
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
  }
  &__content{
    max-width: 29em;
    position: relative;
    z-index: 2;
  }
  &__desc{
    margin-top: 1.25em;
  }
}

.swiper-pagination{
  @include center;
  min-height: 1.125em;
  &-bullet{
    width: 0.625em;
    height: 0.625em;
    border-radius: 50%;
    border: 1px solid #FDFDFD;
    margin: 0 .625em !important;
    background-color: transparent;
    opacity: 1;
    &-active{
      background-color: $primary;
      border-color: $primary;
      width: 1.125em;
      height: 1.125em;
    }
  }
}


@media screen and (max-width: 992px){
  .main-categories{
    padding-top: 1.75em;
    border-radius: 2.5em 2.5em 0 0;
    &__top{
      justify-content: center;
      text-align: center;
    }
    &__link{
      display: none;
    }
  }

  .category-card{
    padding: 1em;
    min-height: 8.25em;
    &__img{
      max-height: 7em;
    }
    &__info{
      gap: .3125em;
      max-width: 14.5em;
    }
  }

  .main{
    margin-bottom: -1.75em;
    padding: 6.5em 0 5.5em;
  }

  .main-slider,
  .product-slider{
    .swiper-pagination{
      bottom: unset;
      top: 100%;
      margin-top: .8125em;
    }
  }

  .main-slider{
    border-radius: 1.25em;
    border-width: 1px;
  }

  .main-card{
    min-height: 15em;
    padding: 1.25em;
    border-radius: 1.25em;
    &__img{
      right: -8em;
      top: 2em;
    }
    &__content{
      max-width: 17.5em;
    }
    &__desc{
      margin-top: .625em;
    }
    &__nav{
      margin-top: 1.25em;
      .m-btn{
        min-width: 11em;
      }
    }
  }

  .swiper-pagination{
    min-height: .75em;
    &-bullet{
      width: 0.5em;
      height: 0.5em;
      &-active{
        width: .75em;
        height: .75em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main-categories{
    &:not(._show-all){
      .category-card:nth-child(4)~.category-card{
        display: none;
      }
    }
    &__more{
      display: flex;
      width: 100%;
      margin-top: 1.875em;
    }
  }
}
