.cart{
  &__items{
    @include grid100gap(1em)
  }
  &__top{
    display: flex;
    padding-left: .875em;
    margin-top: 3.125em;
  }
}

.cart-card{
  padding: 1em 2.5em;
  display: grid;
  grid-template-columns: 17.5em calc(100% - 21em);
  gap: 3.5em;
  position: relative;
  .product-card__img{
    height: 12.25em;
  }
  &__col2{
    padding-top: .5em;
  }
  &__bottom{
    gap: 1.25em;
    margin-top: 1.5em;
  }
  .product-card__info{
    margin-top: 1.25em;
  }
  .product-card__available{
    position: static;
  }
  &__delete{
    position: absolute;
    top: .875em;
    right: .875em;
    width: 2.125em;
    height: 2.125em;
    border-radius: 50%;
    @include center;
    img{
      width: 1.125em;
    }
  }
  .filter-group__check{
    position: absolute;
    top: .875em;
    left: .875em;
    z-index: 2;
    label{
      padding-left: 1.5em;
    }
  }
}

.cart-form{
  padding: 1.875em;
  &__grid{
    margin-top: 1em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: .625em 1em;
  }
  &__textarea{
    grid-column: 2/3;
    grid-row: 1/4;
  }
  &__bottom{
    width: calc(50% - .5em);
    margin-top: 1em;
  }
  &__checks{
    @include grid100gap(.375em);
  }
  .filter-group__check{
    label{
      padding-left: 2.125em;
    }
    span{
      opacity: .4;
    }
  }
  &__btn{
    width: 100%;
    margin-top: 1.25em;
  }
}

.fg{
  &._required{
    input{
      padding-right: 2.5em;
    }
    &:before{
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      width: 1.5em;
      height: 1.5em;
      background-image: url(../img/dot.svg);
      @include bgImgSize(100%);
      right: .875em;
    }
  }
}

.fg--gray{
  textarea,
  input{
    padding: 0 1.125em;
    background-color: #F4F6F7;
    border: none;
    height: 2.875em;
  }
  textarea{
    resize: none;
    min-height: 100%;
    height: 9.875em;
    border-radius: 1em;
    padding: .81em 1.125em;
  }
}


@media screen and (max-width: 992px){
  .cart{
    &__grid{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
    &__top{
      margin-top: 1.25em;
      padding: 0;
    }
  }

  .filter-group__check._big{
    label{
      min-height: 1.875em;
      padding-left: 2.625em;
      &:before{
        width: 1.875em;
        height: 1.875em;
      }
    }
    input:checked~label:before{
      background-size: 1.5625em auto;
    }
  }

  .cart-card{
    padding: 1.25em;
    display: flex;
    flex-direction: column;
    gap: unset;
    &__col2{
      padding-top: .625em;
    }
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between;
      margin-top: 1em;
    }
    .h2{
      font-size: 1.75em;
    }
    .cartcalc{
      order: 2;
    }
    .product-card__info{
      margin-top: .5em;
    }
    &__delete{
      top: unset;
      bottom: 1.375em;
      right: 1.375em;
      width: 2.5em;
      height: 2.5em;
      img{
        width: 1.25em;
      }
    }
    .filter-group__check{
      top: 1em;
      left: 1em;
      label{
        padding-left: 1.875em;
      }
    }
  }

  .fg{
    &._required{
      &:before{
        right: .4375em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .cart{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .cart-form{
    padding: 1.25em 1.125em;
    &__grid{
      margin-top: 1em;
      grid-template-columns: 100%;
      gap: .5em;
    }
    &__textarea{
      grid-column: 1/2;
      grid-row: unset;
    }
    &__bottom{
      width: 100%;
      margin-top: 1.25em;
    }
  }
}
