.header{
  border-radius: 0 0 2.5em 2.5em;
  padding: 1.875em 0;
  background: #000;
  color: $white;
  position: relative;
  z-index: 30;
}

.menu__buttons,
.menu__close,
.burger{
  display: none;
}

.header-bottom{
  margin-top: .625em;
  border-top: 1px solid $dark;
  padding-top: .75em;
  &__inner{
    position: relative;
  }
  &__link{
    display: flex;
    padding: 0 1.25em;
    height: 2.875em;
    margin-right: 1.25em;
    img{
      width: 1em;
      margin-left: .625em;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  gap: 1em;
}

.nav-item__show span,
.nav__link{
  font-size: 1em;
  line-height: 120%;
}

.nav-item{
  position: relative;
  &__show{
    display: flex;
    align-items: center;
    transition: all .3s;
    &:hover{
      color: $primary;
      cursor: pointer;
    }
    &:after{
      content: '';
      width: 1.5em;
      height: 1.5em;
      background-image: url(../img/dot.svg);
      @include bgImgSize(100%);
      margin-left: .5em;
    }
  }
  @media screen and (min-width: 993px){
    &:hover{
      .nav-item__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__inner {
      padding: 1.25em;
      border-radius: 0.875em;
      background: $white;
      box-shadow: $shadow;
      font-family: $font2;
      color: $black;
      white-space: nowrap;
      li{
        position: relative;
        padding-left: 1.5em;
        display: flex;
        align-items: center;
        &:before{
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          transform: translateY(-50%);
          width: .5em;
          height: .5em;
          border-radius: 50%;
          background-color: #D9D9D9;
        }
      }
    }
  }
  &__hidden{
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: -1px;
    padding-top: 1em;
    z-index: 3;
    transition: all .3s;
    pointer-events: none;
    opacity: 0;
  }
  &__back{
    display: none;
  }
  &__inner{
    ul{
      @include grid100gap(.125em);
    }
    a{
      font-size: 1.25em;
      line-height: 150%;
    }
  }
}

.menu-contacts{
  display: none;
}

._full{
  grid-column: 1/-1;
}


@media screen and (max-width: 992px){
  .header{
    padding: .375em 0 .56em;
    border-radius: 0;
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .header-contacts{
        opacity: 0;
        pointer-events: none;
      }
    }
  }

  .menu{
    opacity: 0;
    pointer-events: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background: #000;
    padding-top: 3.75em;
    &__scroll{
      height: 100%;
      overflow-y: auto;
      padding: 0 1em 2.5em;
      display: flex;
      flex-direction: column;
    }
    &__close{
      position: fixed;
      top: .625em;
      right: 1em;
      z-index: 5;
    }
    &__buttons{
      margin-top: auto;
      padding-top: 3em;
      @include grid100gap(1.25em);
      position: relative;
      z-index: 5;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    color: #E0E0E0;
  }

  .menu-item__close,
  .header-catalog__close,
  .menu__close,
  .nav-item__back{
    width: 2.5em;
    height: 2.5em;
    @include center;
    background-color: transparent;
    img{
      width: 100%;
    }
  }

  .nav-item__show span,
  .nav__link{
    font-size: 1.25em;
  }

  .nav-item{
    position: static;
    width: 100%;
    &._open{
      .nav-item__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__show{
      justify-content: space-between;
    }
    &__hidden{
      background: #000;
      position: fixed;
      top: 0;
      margin: 0;
      width: 100%;
      height: $vh;
      padding: 3.75em 1em 10em;
      overflow-y: auto;
    }
    &__back{
      position: fixed;
      top: .625em;
      left: 1em;
    }
    &__inner{
      padding: 0;
      background-color: transparent;
      color: #E0E0E0;
      ul{
        gap: 1em;
      }
      a{
        line-height: 120%;
      }
    }
  }

  .menu-contacts{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
    align-items: flex-start;
    &__item{
      @include grid100gap(.625em);
    }
  }

  .header-bottom{
    margin-top: .5em;
    padding-top: .375em;
    &__link{
      display: none;
    }
  }
}


