.catalog{
  &__title{
    max-width: 50em;
  }
  &__col{
    position: relative;
  }
  &__col2{
    padding-top: 3.5em;
    grid-column: span 3;
  }
  &__rects{
    position: absolute;
    top: 0;
    right: 0;
    width: 1.5625em;
  }
}

.categories-slider{
  margin: -1.375em 0 4.375em;
  .swiper-slide{
    width: 25%;
  }
}

.sort{
  &._open{
    .sort__show img{
      transform: rotate(90deg);
    }
  }
  &__show{
    display: flex;
    align-items: center;
    cursor: pointer;
    img{
      transition: all .3s;
      margin-left: 1em;
      width: 1.625em;
    }
  }
  &__hidden{
    margin-top: .5em;
    display: none;
  }
  &__item{
    border-bottom: 1px solid rgba(#D8D8D8,.5);
    &:last-child{
      border-bottom: none;
    }
    label{
      display: flex;
      align-items: center;
      padding: .625em 0;
      font-size: 1em;
      line-height: 120%;
      min-height: 2.5em;
    }
  }
}

.filter{
  padding: 1.25em;
  border-radius: 1.25em;
  box-shadow: $shadow;
  margin-top: 1.91em;
  &__title{
    font-size: 1.5em;
    line-height: 120%;
    letter-spacing: -.01em;
    font-weight: 700;
    color: $primary;
    text-transform: uppercase;
  }
  &__show{
    pointer-events: none;
    img{
      display: none;
    }
  }
  &__hidden{
    margin-top: 1.375em;
  }
  &__groups{
    @include grid100gap(.625em)
  }
  &__nav{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }
  &__btn{
    height: 2.875em;
  }
}

.filter-group{
  &._open{
    .filter-group__top{
      background-color: $primary;
      color: $white;
      path{
        stroke: $white;
      }
      svg{
        transform: rotate(45deg);
      }
    }
  }
  &__top{
    padding: 1em 1.5em;
    min-height: 3.5625em;
    border-radius: 1.25em;
    background: rgba(42, 42, 42, 0.10);
    transition: all .3s;
    cursor: pointer;
    svg{
      width: 1.625em;
    }
  }
  &__bottom{
    padding: 1.25em 1.5em .625em;
    display: none;
  }
  &__checks{
    @include grid100gap(.625em);
  }
  &__check{
    label{
      position: relative;
      min-height: 1.5em;
      display: flex;
      align-items: center;
      padding-left: 2.5em;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 1.5em;
        height: 1.5em;
        background-image: url(../img/plus.svg);
        @include bgImgSize(0);
        border-radius: 50%;
        border: 1px solid rgba(239, 59, 36, 0.20);
        background-color: #F4F6F7;
      }
    }
    input:checked~label:before{
      background-size: 1.25em auto;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog{
    overflow: hidden;
    &__rects{
      display: none;
    }
    &__title{
      max-width: 70%;
    }
    &__grid{
      margin-top: .875em;
      grid-template-columns: 100%;
      gap: 2.5em;
    }
    &__col2{
      padding: 0;
      grid-column: 1/-1;
    }
  }

  .categories-slider{
    margin: -1.25em 0 2.5em;
    .swiper-slide{
      width: 14em;
    }
    .category-card{
      min-height: 6.5em;
      &__img{
        max-height: 4.5em;
      }
      &__count{
        display: none;
      }
      &__title{
        font-size: .875em;
      }
    }
  }

  .sort,
  .filter{
    &__show{
      min-height: 3.125em;
      padding: 0 1em;
      justify-content: space-between;
      border-radius: 1.875em;
      border: 1px solid #D8D8D8;
      background: $white;
      box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, 0.05);
      position: relative;
      z-index: 2;
    }
    &__hidden{
      margin-top: -1.25em;
      padding-top: 1.5em;
      border-radius: 0 0 1.25em 1.25em;
      background: $white;
      box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, 0.05);
    }
  }

  .sort{
    &__item{
      label{
        padding: .625em 1em;
      }
    }
  }

  .filter{
    padding: 0;
    border-radius: 0;
    box-shadow: unset;
    margin-top: 1em;
    &__title{
      font-size: 1em;
      text-transform: unset;
    }
    &__show{
      img{
        display: block;
        width: 1.25em;
      }
    }
    &__hidden{
      padding: 2.25em .75em .75em;
    }
    &__groups{
      gap: .5em;
    }
    &__nav{
      margin-top: 1em;
    }
  }

  .filter-group{
    &__top{
      padding: .5em 1.5em;
      min-height: 2.8125em;
    }
    &__bottom{
      padding: .9375em 0;
    }
    &__checks{
      grid-template-columns: repeat(2,calc(50% - .3125em));
    }
  }
}


