.m-section{
  padding: 3.125em 0;
  &--m{
    margin: 3.125em 0;
  }
  &._first{
    padding-top: 0;
    margin-top: 0;
  }
}

.consult{
  &__inner{
    display: grid;
    grid-template-columns: 22.75em 22.75em;
    align-items: flex-end;
    justify-content: space-between;
    border-radius: 1.25em;
    background-color: #FDFCFC;
    background-image: url(../img/consult-icon.svg);
    background-repeat: no-repeat;
    background-position: right top;
    background-size: 5.1875em auto;
    box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, 0.05);
    padding: 2.5em;
    position: relative;
  }
  &__col,
  &__col2{
    z-index: 2;
    position: relative;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 25.25em;
    height: 100%;
  }
}

.consult-form{
  &__fields{
    @include grid100gap(1em);
  }
}

._w-100{
  width: 100%;
}

.consult-info{
  padding: 1.875em;
  border-radius: 1.25em;
  background: #F4F6F7;
  box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, 0.05);
  &__top{
    max-width: 15em;
  }
}

.faq{
  &__inner{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }
  &__col{
    max-width: 43.875em;
  }
  &__items{
    @include grid100gap(1em);
  }
}

.qa-card{
  background: $white;
  box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, 0.05);
  border-radius: 1.25em;
  &._active{
    .qa-card__btn {
      background-color: $primary;
      img{
        transform: rotate(90deg);
      }
    }
  }
  &__show{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.25em 1.25em 1.25em 1.875em;
  }
  &__btn{
    margin-left: 1em;
    width: 2.75em;
    height: 2.75em;
    border-radius: 50%;
    background-color: $black;
    @include center;
    flex-shrink: 0;
    img{
      transition: all .3s;
      width: 1.625em;
    }
  }
  &__hidden{
    padding: 0 4em 2em 1.875em;
    display: none;
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 1.25em 0;
    &--m{
      margin: 1.25em 0;
    }
  }

  .consult{
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 12.75em 1em;
      padding: 1em;
      background-image: unset;
    }
    &__img{
      left: unset;
      right: 0;
      width: 25em;
      height: unset;
      max-width: 100%;
    }
  }

  .consult-form{
    &__fields{
      gap: .5em;
    }
  }

  .consult-info{
    padding: 1.25em;
  }

  .faq{
    &__items{
      gap: .5em;
    }
  }

  .qa-card{
    &__show{
      padding: .625em .625em .625em 1.25em;
    }
    &__btn{
      width: 2.125em;
      height: 2.125em;
    }
    &__hidden{
      padding: 0 1.25em 1.25em 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .consult{
    .container{
      padding: 0;
    }
    &__inner{
      grid-template-columns: 100%;
      border-radius: 2.5em;
      padding-top: 0;
    }
    &__col2{
      padding: 0 .5625em;
    }
    &__img{
      top: unset;
      bottom: 9em;
    }
  }

  .consult-form{
    &__top{
      text-align: center;
    }
  }

  .faq{
    &__inner{
      grid-template-columns: 100%;
    }
    &__col{
      text-align: center;
    }
  }
}
