.article-bottom{
  margin-top: 3.5em;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  &__col2{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 1em;
  }
  .socials{
    margin-top: 1em;
  }
}

.object{
  .project-card__tag{
    color: #909090;
  }
  &__images{
    @include grid100gap(1em);
    img{
      width: 100%;
      display: block;
      border-radius: 1.25em;
    }
  }
}

.object-info{
  padding: 1.875em;
  box-shadow: $shadow;
  background-color: $white;
  border-radius: 1.25em;
  position: sticky;
  top: 1em;
  &__tags{
    margin-top: .875em;
    display: flex;
    flex-wrap: wrap;
    gap: 1em;
  }
}


@media screen and (max-width: 992px){
  .article-bottom{
    margin-top: 1.25em;
    text-align: center;
    justify-content: center;
    &__col2{
      display: none;
    }
    .socials{
      margin-top: .625em;
      justify-content: center;
    }
  }

  .object{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }

  .object-info{
    padding: 0;
    box-shadow: unset;
    background-color: transparent;
    &__tags{
      margin-top: .625em;
      justify-content: center;
      gap: .5em;
    }
  }
}


