.trust{
  &__top{
    max-width: 45.5em;
  }
  &__grid{
    margin-top: 2.5em;
  }
}

.trust-video,
.m-video{
  &__img{
    min-height: 100%;
    @include imgCover(63.9%);
    border-radius: inherit;
    &:after{
      border-radius: 1.25rem;
      background: linear-gradient(0deg, rgba(45, 45, 45, 0.70) 0%, rgba(45, 45, 45, 0.70) 100%);
    }
    &:before{
      content: '';
      @include centerAbsolute;
      width: 6.875em;
      height: 6.875em;
      background-image: url(../img/play.svg);
      @include bgImgSize(100%);
      border-radius: 50%;
      box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
      z-index: 3;
    }
  }
}

.trust-video{
  grid-column: span 2;
  border-radius: 1.25em;
  background: $white;
  box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, 0.05);
  padding: 1.5em;
}

.trust-item{
  margin-bottom: .3125em;
  display: flex;
  align-items: center;
  gap: .3125em 1.25em;
  border-radius: 1.25em;
  background: #FFF;
  box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, 0.05);
  padding: 1.25em 1.5em 1.25em 1.81em;
  min-height: 7.5em;
  color: $gray3;
  &:last-child{
    margin: 0;
  }
  &__num{
    @include sameNumWidth;
    font-size: 3.5em;
    line-height: 110%;
    color: $primary;
  }
}

.trust-about{
  position: relative;
  border-radius: 1.25em;
  overflow: hidden;
  box-shadow: 2px 4px 6px 0 rgba(0, 0, 0, 0.05);
  min-height: 30em;
  color: $white;
  &:after{
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 66.16%, rgba(0, 0, 0, 0.80) 100%);
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100%;
    object-fit: cover;
  }
  &__bottom{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 1.875em;
    z-index: 3;
  }
}


@media screen and (max-width: 992px){
  .trust{
    &__top{
      max-width: 100%;
      text-align: center;
      br{
        display: none;
      }
    }
    &__items{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
    }
  }

  .trust-video{
    box-shadow: none;
    padding: 0;
  }

  .trust-item{
    margin: 0;
    flex-direction: column;
    align-items: flex-start;
    background: #F4F6F7;
    padding: 1.25em;
    &__num{
      font-size: 2.5em;
    }
  }

  .trust-about{
    min-height: 10.5em;
    &__img{
      top: -5em;
      min-height: calc(100% + 5em);
    }
    &__bottom{
      padding: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .trust-video{
    grid-column: 1/-1;
    &__img{
      &:before{
        width: 4.75em;
        height: 4.75em;
      }
    }
  }
}
