.footer{
  padding: 4em 0 3.5em;
  border-radius: 6.25em 6.25em 0 0;
  background: #080A0B;
  color: $white;
  margin-top: 3.125em;
  position: relative;
}

.scroll-top{
  width: 2.75em;
  @include center;
  position: absolute;
  right: 1em;
  bottom: 100%;
  margin-bottom: 2.5em;
  background-color: transparent;
  img{
    width: 100%;
  }
}

.footer-middle,
.footer-top{
  display: grid;
  grid-template-columns: 22.75em 19em 19em 22.75em;
  justify-content: space-between;
  align-items: flex-start;
}

.footer-top{
  &__col{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1.5em 1em;
  }
}

.footer-nav{
  &--catalog-mob{
    display: none;
  }
  &__links{
    margin-top: 1em;
    @include grid100gap(1em);
  }
}

.footer-middle{
  margin-top: 2.5em;
  &__col2,
  &__col3,
  &__col4{
    padding-top: 2.31em;
  }
  &__logo{
    display: block;
    width: 10em;
    margin-bottom: 1.875em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__buttons{
    @include grid100gap(1.5625em);
  }
  &__desc{
    color: #636363;
  }
}

.footer-address{
  @include grid100gap(.5em);
  width: calc(100% + 2em);
}

.footer-contacts{
  @include grid100gap(1.375em);
  &__item{
    @include grid100gap(.5em);
  }
}

.footer-bottom{
  margin-top: 2.81em;
  padding-top: 2.375em;
  border-top: 1px solid #2D2D2D;
  &__col{
    gap: 1.81em;
  }
  &__col2{
    gap: 9.25em;
  }
  &__desc{
    display: none;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 7.375em 0 1.375em;
    border-radius: 2.5em 2.5em 0 0;
    position: relative;
    margin-top: 1.25em;
  }

  .scroll-top{
    display: none;
  }

  .footer-top{
    grid-template-columns: 100%;
    padding-top: .625em;
    border-top: 1px solid #2D2D2D;
    &__col{
      @media screen and (min-width: 577px){
        grid-template-columns: calc(100% - 26em) 12em 12em;
        justify-content: space-between;
      }
    }
    &__col2,
    &__col3,
    &__col4{
      display: none;
    }
  }

  .footer-nav{
    &--catalog-mob{
      display: block;
    }
    &__links{
      margin-top: .9375em;
      gap: .625em;
    }
  }

  .footer-middle{
    grid-template-columns: calc(100% - 26em) 25em;
    &__col2{
      position: absolute;
      top: 3.375em;
      left: 0;
      width: 100%;
      text-align: center;
      padding: 0 .9375em;
    }
    &__col{
      margin: 0;
      position: absolute;
      top: 1.25em;
      left: 50%;
      transform: translateX(-50%);
    }
    &__col3{
      order: 2;
      padding: 0;
    }
    &__col4{
      padding: 0;
    }
    &__logo{
      width: 5.875em;
    }
    &__buttons{
      gap: .94em;
    }
    &__desc{
      display: none;
    }
  }

  .footer-address{
    width: 100%;
  }

  .footer-contacts{
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }

  .footer-bottom{
    margin: 1.5em auto 0;
    padding-top: .9375em;
    width: calc(100% - 1.875em);
    .container{
      padding: 0;
    }
    &__inner{
      align-items: flex-start;
    }
    &__col,
    &__col2{
      gap: .625em;
      flex-direction: column;
      align-items: flex-start;
    }
    &__col{
      flex-shrink: 0;
      padding-right: 2em;
    }
    &__desc{
      margin-top: 1.56em;
      display: block;
      color: #636363;
    }
  }
}


@media screen and (max-width: 576px){
  .footer-middle{
    margin-top: 1.56em;
    grid-template-columns: 100%;
    gap: 1.56em;
    &__col3{
      order: unset;
    }
  }

  .footer-nav{
    &--catalog-mob{
      grid-column: 1/-1;
    }
  }
}
