.produce-card{
  height: 100%;
  min-height: 36em;
  position: relative;
  color: $white;
  display: flex;
  align-items: center;
  padding: 7em 3.125em;
  &__img{
    @include imgCover(0);
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 3.75em;
    z-index: -1;
    &:after{
      background: linear-gradient(266deg, rgba(0, 0, 0, 0.00) 8.43%, #000 65.53%);
    }
    ._view2{
      display: none;
    }
  }
  &__content{
    max-width: 47em;
  }
  &__desc{
    margin-top: 1.25em;
    max-width: 42em;
  }
}

.trust-info{
  padding: 1em;
  color: #636363;
  .text{
    gap: .625em;
    p{
      line-height: 120%;
    }
  }
  &__items{
    margin-top: 1.25em;
    @include grid100gap(1.125em);
  }
  &__item{
    padding-left: 2.125em;
    background-image: url(../img/check2.svg);
    background-size: 1.25em auto;
    background-position: 0 0;
    background-repeat: no-repeat;
  }
}

.m-video{
  &__img{
    padding-bottom: 52%;
    border-radius: 1.25em;
  }
}

.produce-overview{
  &:not(._show-all){
    .m-video:nth-child(2)~.m-video{
      display: none;
    }
  }
}

.produce-gallery{
  &:not(._show-all){
    .produce-gallery__img:nth-child(2)~.produce-gallery__img{
      display: none;
    }
  }
  &__desc{
    max-width: 31em;
  }
  &__img{
    @include imgCover(52%);
    border-radius: 1.25em;
  }
}



@media screen and (max-width: 992px){
  .produce-card{
    min-height: 23em;
    padding: 1em;
    display: block;
    &__img{
      border-radius: 1.25em;
    }
    &__desc{
      margin-top: .5em;
      max-width: 17.5em;
    }
    &__link{
      min-width: 11em;
    }
    .h1{
      letter-spacing: -.02em;
    }
  }

  .trust-info{
    .text{
      gap: .5em;
    }
    &__items{
      margin-top: .5em;
      gap: .5em;
    }
    &__item{
      padding-left: 1.625em;
      min-height: 1.25em;
      padding-top: .09em;
    }
  }

  .produce-overview{
    ._toggle-more{
      width: 100%;
      min-width: unset;
    }
  }

  .produce-gallery{
    &__desc{
      max-width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .produce-card{
    &__img{
      ._view2{
        display: block;
      }
      ._view1{
        display: none;
      }
    }
  }
}
