.contacts{
  &__items{
    margin-top: 2.75em;
  }
  &__download{
    margin-top: 1.44em;
    width: 100%;
  }
  &__tabs{
    margin: 5.625em 0 5em;
  }
  &__tab{
    color: $dark;
    cursor: pointer;
    padding: 0;
    &._active{
      pointer-events: none;
      background-color: $primary;
      color: $white;
    }
  }
  &__bottom{
    margin-top: 5.625em;
    .map{
      margin-top: 1.25em;
    }
  }
}

.contacts-item{
  border-radius: 1.25em;
  background: #F4F6F7;
  border: 1px solid #F4F6F7;
  box-shadow: $shadow;
  padding: 1.25em 1.875em;
  transition: all .3s;
  &._white{
    background: transparent;
    border-color: #DED9D6;
  }
  &__icon{
    height: 2.75em;
    margin-right: 1em;
  }
  &__info{
    margin-top: 1em;
    @include grid100gap(.625em);
  }
  &__title{
    color: #AFADAA;
  }
  &__link{
    margin: -.25em 0;
    color: $primary;
    img{
      width: 1.625em;
      margin-left: .25em;
    }
  }
  &__top{
    display: flex;
    align-items: flex-start;
    .contacts-item__info{
      gap: .875em;
      margin-top: .125em;
    }
  }
  &__phone{
    font-size: 1.625em;
    line-height: 115%;
    font-weight: 700;
    color: $primary;
  }
  &__btn{
    width: 100%;
    margin-top: 1em;
  }
}

.contact-card{
  padding: 1.44em 4em 1.44em 1.44em;
  min-height: 17.8125em;
  background-image: url(../img/consult-icon.svg);
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: 8.4375em auto;
  position: relative;
  &__title{
    font-style: italic;
    color: #808080;
  }
  &__phone{
    gap: 0 .69em;
    margin-top: 1.25em;
    .text16{
      color: #AFADAA;
      font-style: italic;
    }
  }
  &__phones{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: 16em calc(100% - 18.5em);
    gap: 1em 2.5em;
    align-items: flex-start;
    .contact-card__phone{
      margin: 0;
      align-items: flex-start;
      flex-direction: column;
    }
  }
  &__top{
    padding-right: 10em;
  }
  &__img{
    position: absolute;
    top: 1em;
    right: 1em;
    width: 13.625em;
  }
}

.map{
  height: 31.25em;
  border-radius: 1.25em;
  overflow: hidden;
}


@media screen and (max-width: 992px){
  .contacts{
    &__items{
      margin-top: 1.25em;
    }
    &__download{
      margin-top: .625em;
    }
    &__tabs{
      margin: 3.125em 0 1.875em;
    }
    &__bottom{
      margin-top: 2.5em;
    }
  }

  .contacts-item{
    padding: 1em;
    &__icon{
      height: 1.875em;
      margin-right: .75em;
    }
    &__info{
      margin-top: .69em;
      gap: .3125em;
    }
    &__link{
      margin: -.125em 0;
    }
    &__content{
      padding-top: .25em;
    }
    &__phone{
      font-size: 1.25em;
    }
    &__btn{
      margin-top: 1.25em;
    }
  }

  .contact-card{
    padding: 1em;
    min-height: unset;
    background-size: 4.3125em auto;
    &__phone{
      align-items: flex-start;
      flex-direction: column;
      margin-top: .625em;
    }
    &__phones{
      margin-top: .625em;
      grid-template-columns: 100%;
      max-width: 15.5em;
    }
    &__top{
      padding-right: 10em;
    }
    &__img{
      top: .5em;
      right: .5em;
      width: 8em;
    }
  }

  .map{
    height: 18.75em;
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__items{
      gap: .625em;
    }
  }
}
