.header-top{
  &__inner{
    min-height: 2.75em;
    display: flex;
    align-items: center;
    position: relative;
  }
  &__logo{
    @include centerAbsolute;
    width: 10em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.header-action{
  display: flex;
  gap: .625em;
  &__btn{
    width: 2.75em;
    height: 2.75em;
    background-color: $dark;
    border-radius: 50%;
    @include center;
    img{
      width: 1.25em;
    }
  }
}

.header-contacts{
  flex-grow: 1;
  margin-right: 2.5em;
  &__hidden{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: .5em 2.5em;
  }
  &__street{
    color: #E3E3E3;
    margin-right: auto;
  }
  &__show{
    display: none;
  }
}

.header-index{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  border-radius: 0;
}


@media screen and (max-width: 992px){
  .header-top{
    &__inner{
      min-height: 1.875em;
      position: static;
      justify-content: space-between;
    }
    &__logo{
      position: static;
      transform: unset;
      width: 5.875em;
    }
  }

  .header-contacts{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: .375em;
    z-index: 4;
    &__show{
      @include center;
      width: 1.875em;
      img{
        width: 100%;
      }
    }
    &__hidden{
      position: absolute;
      top: 100%;
      margin-top: 1.125em;
      left: 50%;
      transform: translateX(-50%);
      border-radius: 1.25em;
      background: #000;
      box-shadow: $shadow;
      width: 17.75em;
      flex-direction: column-reverse;
      text-align: center;
      z-index: 3;
      padding: 1.875em 2.125em;
      pointer-events: none;
      opacity: 0;
      transition: all .3s;
      &:before{
        content: '';
        position: absolute;
        left: 50%;
        top: -.5em;
        transform: translateX(-50%) rotate(-45deg);
        width: 1em;
        height: 1em;
        border-radius: .25em;
        background: inherit;
        box-shadow: $shadow;
      }
    }
    &__street{
      margin-top: .5em;
    }
    &._open{
      .header-contacts__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .header-action{
    position: absolute;
    bottom: .562em;
    right: 1em;
    &__btn{
      width: 1.875em;
      height: 1.875em;
      img{
        width: 1em;
      }
    }
  }

  .burger{
    display: flex;
    align-items: center;
    padding: 0 .375em 0 1em;
    height: 1.875em;
    img{
      width: 1.25em;
      margin-left: .625em;
    }
    span{
      font-size: .875em;
    }
  }
}


