.text{
  @include grid100gap(1em);
  p{
    line-height: 138%;
  }
  li{
    padding-left: 1.875em;
    position: relative;
    &:before{
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  hr{
    opacity: 0;
    margin: 0;
    display: block;
  }
  ol {
    counter-reset: list;
    li {
      counter-increment: list;
      &:before {
        content: counter(list) '. ';
      }
    }
  }
  ul{
    li{
      line-height: 138%;
      &:before{
        content: '';
        width: .375em;
        height: .375em;
        position: absolute;
        border-radius: 50%;
        background-color: $primary;
        top: .5em;
        left: .625em;
      }
    }
  }
  h2,
  h3{
    font-size: 1.5em;
    line-height: 110%;
    color: $primary;
  }
}



@media screen and (max-width: 992px){
  .text{
    h2,
    h3{
      font-size: 1.125em;
    }
  }
}


